/**
 * TradingView Widget 
 *  widget 
 */

class WidgetThrottle {
  constructor() {
    this.maxConcurrent = 6 //  6 
    this.currentLoading = 0
    this.queue = []
    this.loadDelay = 100 //  100ms 
  }

  /**
   *  widget 
   */
  async addToQueue(loadFunction) {
    return new Promise((resolve, reject) => {
      this.queue.push({
        loadFunction,
        resolve,
        reject,
        timestamp: Date.now()
      })
      
      this.processQueue()
    })
  }

  /**
   * 
   */
  async processQueue() {
    if (this.currentLoading >= this.maxConcurrent || this.queue.length === 0) {
      return
    }

    const task = this.queue.shift()
    this.currentLoading++

    try {
      // 
      if (this.currentLoading > 1) {
        await this.delay(this.loadDelay)
      }

      const result = await task.loadFunction()
      task.resolve(result)
    } catch (error) {
      task.reject(error)
    } finally {
      this.currentLoading--
      
      // 
      setTimeout(() => {
        this.processQueue()
      }, 25) //  25ms
    }
  }

  /**
   * 
   */
  delay(ms) {
    return new Promise(resolve => setTimeout(resolve, ms))
  }

  /**
   * 
   */
  getStatus() {
    return {
      currentLoading: this.currentLoading,
      queueLength: this.queue.length,
      maxConcurrent: this.maxConcurrent
    }
  }

  /**
   * 
   */
  clearQueue() {
    this.queue.forEach(task => {
      task.reject(new Error('Queue cleared'))
    })
    this.queue = []
  }

  /**
   * 
   */
  setMaxConcurrent(max) {
    this.maxConcurrent = Math.max(1, Math.min(10, max))
  }
}

// 
export const widgetThrottle = new WidgetThrottle()

export default widgetThrottle