<template>
  <div class="stock-overview">
    <div class="stock-header">
      <h2>Stock Overview</h2>
      <div class="header-right">
        <div class="update-info" v-if="lastUpdate">
          <span class="text-muted">Last updated: {{ formatTime(lastUpdate) }}</span>
        </div>
      </div>
    </div>
    
    <p class="text-muted mb-3">Stock universe overview and analysis</p>



    <div v-if="loading" class="loading-with-skeleton">
      <SkeletonLoader />
    </div>

    <div v-else-if="error" class="error">
      <p class="text-danger">{{ error }}</p>
      <button @click="refresh" class="btn btn-secondary">Retry</button>
    </div>

    <div v-else>
      <!-- Market Index Section -  Market Overview -->
      <div class="widget-container-ticker">
        <div class="widget-header">
          <h3>Market Index</h3>
        </div>
        <LazyTradingViewWidget
          widget-type="Market Index"
          :config="tickersConfig"
          script-url="https://s3.tradingview.com/external-embedding/embed-widget-tickers.js"
          height="100px"
          :priority="1"
        />
      </div>
      
      <!-- Sector Groups -->
      <div class="sector-groups">
        <div 
          v-for="(group, sector) in groupedStocks" 
          :key="sector"
          class="sector-group"
        >
          <h4 class="sector-title">
            {{ sector }}
            <span class="stock-count">({{ group.length }})</span>
          </h4>
          
          <div class="stocks-in-group">
            <StockCard
              v-for="stock in group"
              :key="stock.quote.symbol"
              :quote="stock.quote"
              :daily-data="stock.dailyData"
              :metadata="stock.metadata"
            />
          </div>
        </div>
      </div>
    </div>

    <div v-if="!loading && Object.keys(groupedStocks).length === 0" class="no-data">
      <p>No stock data available</p>
    </div>
  </div>
</template>

<script>
import { dataFetcher } from '@/lib/fetcher'
import { symbolsConfig } from '@/utils/symbolsConfig'
import StockCard from './StockCard.vue'
import LazyTradingViewWidget from './LazyTradingViewWidget.vue'
import SkeletonLoader from './SkeletonLoader.vue'
import { metadataService } from '@/utils/metadataService.js'
import { performanceCache, CACHE_KEYS, CACHE_TTL } from '@/utils/performanceCache.js'
import { performanceMonitor, PERFORMANCE_LABELS } from '@/utils/performanceMonitor.js'

export default {
  name: 'StockOverview',
  components: {
    StockCard,
    LazyTradingViewWidget,
    SkeletonLoader
  },
  data() {
    return {
      quotes: [],
      dailyData: null,
      metadata: null,
      loading: false,
      error: null,
      lastUpdate: null,
      staleLevel: 'fresh',
      configuredSymbols: [],
      configSource: 'static',
      cacheInfo: {},
      // 
      loadingStages: {
        config: false,
        quotes: false,
        dailyData: false,
        metadata: false
      }
    }
  },
  computed: {
    tickersConfig() {
      return {
        "symbols": [
          {"proName": "FOREXCOM:SPXUSD","title": "S&P 500 Index"},
          {"proName": "NASDAQ:NDX","title": "NASDAQ 100 Index"},
          {"proName": "OPOFINANCE:DJIUSD","title": "Dow Jone Index"},
          {"proName": "CAPITALCOM:RTY","title": "US Russel 2000"},
          {"proName": "INDEX:BTCUSD","title": "BTC"},
          {"proName": "TVC:GOLD","title": "GOLD"}
        ],
        "colorTheme": "light",
        "locale": "en",
        "largeChartUrl": "",
        "isTransparent": true,
        "showSymbolLogo": true
      }
    },

    groupedStocks() {
      if (!this.quotes.length || !this.metadata) return {}
      
      const groups = {}
      
      this.quotes.forEach(quote => {
        const symbolMetadata = this.metadata.items.find(m => m.symbol === quote.symbol)
        const symbolDailyData = this.dailyData?.per_symbol.find(d => d.symbol === quote.symbol)
        
        //  PRD confidence >= 0.7  sector
        let sector = 'Unknown'
        if (symbolMetadata && symbolMetadata.confidence >= 0.7) {
          sector = symbolMetadata.sector || 'Unknown'
        }
        
        if (!groups[sector]) {
          groups[sector] = []
        }
        
        groups[sector].push({
          quote,
          dailyData: symbolDailyData,
          metadata: symbolMetadata
        })
      })
      
      //  sector  sector 
      const sortedGroups = {}
      Object.keys(groups)
        .sort() //  sector 
        .forEach(sector => {
          //  sector 
          // 1.  exchange ()
          // 2.  industry ()
          // 3.  symbol A-Z
          sortedGroups[sector] = groups[sector].sort((a, b) => {
            //  exchange 
            const exchangeA = this.getStockExchange(a.quote.symbol, a.metadata)
            const exchangeB = this.getStockExchange(b.quote.symbol, b.metadata)
            
            //  exchange
            if (exchangeA !== exchangeB) {
              return exchangeA.localeCompare(exchangeB)
            }
            
            //  industry 
            const industryA = this.getStockIndustry(a.metadata)
            const industryB = this.getStockIndustry(b.metadata)
            
            //  industry
            if (industryA !== industryB) {
              return industryA.localeCompare(industryB)
            }
            
            //  symbol A-Z
            return a.quote.symbol.toUpperCase().localeCompare(b.quote.symbol.toUpperCase())
          })
        })
      
      return sortedGroups
    }
  },
  async mounted() {
    performanceMonitor.start(PERFORMANCE_LABELS.STOCK_OVERVIEW_LOAD)
    await this.loadSymbolsConfig()
    await this.loadStockData()
    performanceMonitor.end(PERFORMANCE_LABELS.STOCK_OVERVIEW_LOAD)
    
    // 
    const report = performanceMonitor.generateReport()
    performanceMonitor.checkPerformanceWarnings()
  },
  methods: {
    async loadSymbolsConfig() {
      this.loadingStages.config = true
      try {
        await performanceMonitor.measureAsync(PERFORMANCE_LABELS.SYMBOLS_CONFIG_LOAD, async () => {
          this.configuredSymbols = await symbolsConfig.getSymbolsList()
          this.configSource = symbolsConfig.getConfigSource()
          this.cacheInfo = symbolsConfig.getCacheInfo()
        })
        
        console.log(` Loaded ${this.configuredSymbols.length} symbols from ${this.configSource}:`, this.configuredSymbols)
        console.log(' Cache info:', this.cacheInfo)
        
        // 
        const expectedSymbols = ['ASTS', 'RIVN', 'PL', 'ONDS', 'RDW', 'AVAV', 'MDB', 'ORCL', 'TSM', 'RKLB', 'CRM', 'NVDA', 'AVGO', 'AMZN', 'GOOG', 'META', 'NFLX', 'LEU', 'SMR', 'CRWV', 'IONQ', 'PLTR', 'HIMS', 'TSLA']
        const missingSymbols = expectedSymbols.filter(symbol => !this.configuredSymbols.includes(symbol))
        if (missingSymbols.length > 0) {
          console.warn(' Missing symbols:', missingSymbols)
        }
        
      } catch (error) {
        console.warn(' Failed to load symbols config:', error)
        this.configuredSymbols = symbolsConfig.getStaticSymbols()
        this.configSource = 'static'
        this.cacheInfo = symbolsConfig.getCacheInfo()
        console.log(` Fallback to static symbols (${this.configuredSymbols.length}):`, this.configuredSymbols)
      } finally {
        this.loadingStages.config = false
      }
    },

    getStockExchange(symbol, metadata) {
      if (metadata && metadata.exchange) {
        return metadata.exchange
      }
      
      //  symbol 
      // NYSE 
      if (['ORCL', 'TSM', 'RDW'].includes(symbol)) {
        return 'NYSE'
      }
      // NASDAQ 
      else if (['ASTS', 'RIVN', 'PL', 'ONDS', 'AVAV', 'MDB', 'RKLB', 'CRM', 'NVDA', 'AVGO', 'AMZN', 'GOOG', 'META', 'NFLX', 'LEU', 'SMR', 'CRWV', 'IONQ', 'PLTR', 'HIMS', 'TSLA'].includes(symbol)) {
        return 'NASDAQ'
      }
      
      return 'NASDAQ' // 
    },

    getStockIndustry(metadata) {
      if (!metadata) return 'Unknown Industry'
      
      //  PRD confidence < 0.7  Unknown
      if (metadata.confidence < 0.7) {
        return 'Unknown Industry'
      }
      
      //  industry  sector
      return metadata.industry || metadata.sector || 'Unknown Industry'
    },

    async loadStockData() {
      this.loading = true
      this.error = null
      
      try {
        //  
        const cachedData = performanceCache.get(CACHE_KEYS.STOCK_OVERVIEW_DATA)
        if (cachedData) {
          console.log(' Using cached stock overview data')
          this.quotes = cachedData.quotes
          this.dailyData = cachedData.dailyData
          this.metadata = cachedData.metadata
          this.lastUpdate = cachedData.lastUpdate
          this.staleLevel = cachedData.staleLevel
          this.loading = false
          return
        }

        //   API
        //  Yahoo Finance API 
        metadataService.setUseDynamicAPI(false)
        
        // 
        this.loadingStages.quotes = true
        this.loadingStages.dailyData = true
        
        const [quotesResult, dailyResult] = await Promise.all([
          dataFetcher.fetchQuotesSnapshot(),
          dataFetcher.fetchDailySnapshot()
        ])
        
        //  quotes -  symbols
        if (quotesResult.data && quotesResult.data.items) {
          //  symbols
          const allQuotes = quotesResult.data.items
          this.quotes = allQuotes.filter(quote => 
            this.configuredSymbols.includes(quote.symbol)
          )
          
          console.log(` Total quotes available: ${allQuotes.length}`)
          console.log(` Filtered quotes for configured symbols: ${this.quotes.length}`)
          console.log(` Quote symbols:`, this.quotes.map(q => q.symbol).sort())
          
          this.lastUpdate = quotesResult.as_of
          this.staleLevel = quotesResult.stale_level
        }
        this.loadingStages.quotes = false
        
        //  daily data
        if (dailyResult.data) {
          this.dailyData = dailyResult.data
        }
        this.loadingStages.dailyData = false
        
        //  metadata ()
        if (this.quotes.length > 0) {
          this.loadingStages.metadata = true
          const symbols = this.quotes.map(quote => quote.symbol)
          console.log(` Loading metadata for ${symbols.length} symbols using static data...`)
          
          const metadataMap = await metadataService.getBatchMetadata(symbols)
          
          // 
          this.metadata = {
            items: Array.from(metadataMap.values()),
            as_of: new Date().toISOString(),
            source: 'Static Data (Performance Optimized)'
          }
          
          console.log(` Successfully loaded metadata for ${this.metadata.items.length} symbols`)
          this.loadingStages.metadata = false
        }
        
        //  
        const dataToCache = {
          quotes: this.quotes,
          dailyData: this.dailyData,
          metadata: this.metadata,
          lastUpdate: this.lastUpdate,
          staleLevel: this.staleLevel
        }
        performanceCache.set(CACHE_KEYS.STOCK_OVERVIEW_DATA, dataToCache, CACHE_TTL.QUOTES)
        console.log(' Cached stock overview data for future use')
        
        // 
        if (!this.quotes.length && !this.dailyData && !this.metadata) {
          throw new Error('No stock data available')
        }
        
      } catch (err) {
        this.error = String(err)
        console.error('Failed to load stock data:', err)
        // 
        Object.keys(this.loadingStages).forEach(key => {
          this.loadingStages[key] = false
        })
      } finally {
        this.loading = false
      }
    },

    async refresh() {
      // 
      performanceCache.delete(CACHE_KEYS.STOCK_OVERVIEW_DATA)
      console.log(' Cleared stock overview cache')
      
      //  symbols 
      await symbolsConfig.refresh()
      await this.loadSymbolsConfig()
      await this.loadStockData()
    },

    formatTime(timeString) {
      if (!timeString) return ''
      
      try {
        const date = new Date(timeString)
        return date.toLocaleString('en-US', {
          month: 'short',
          day: 'numeric',
          hour: '2-digit',
          minute: '2-digit'
        })
      } catch {
        return timeString
      }
    }
  }
}
</script>

<style scoped>
.stock-overview {
  margin-bottom: 1rem;
}

.stock-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 0.75rem;
  gap: 1rem;
}

.stock-header h2 {
  font-size: 1.5rem;
  font-weight: 700;
  color: #333;
  margin: 0;
  flex-shrink: 0;
}

.header-right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 0.25rem;
}

.header-right .text-muted {
  margin: 0;
  font-size: 0.9rem;
}

.header-right .mb-3 {
  margin-bottom: 0;
}

.config-info {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.85rem;
}

.config-source {
  color: #007bff;
  font-weight: 500;
}

.symbols-count {
  color: #6c757d;
  font-style: italic;
}

.cache-info {
  color: #28a745;
  font-weight: 500;
  font-size: 0.8rem;
}

.update-info {
  display: flex;
  align-items: center;
  gap: 1rem;
  font-size: 0.9rem;
}

/*  Widget  */
.widget-container-ticker {
  background: white;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 2rem;
  overflow: hidden;
  position: relative;
}

.widget-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  padding-bottom: 0.75rem;
  border-bottom: 1px solid #f0f0f0;
}

.widget-header h3 {
  font-size: 1.1rem;
  font-weight: 600;
  color: #333;
  margin: 0;
}

.sector-groups {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.sector-group {
  background: #f8f9fa;
  border-radius: 8px;
  padding: 1.5rem;
}

.sector-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.stock-count {
  font-size: 0.8rem;
  color: #666;
  font-weight: 400;
}

/* Stock Cards Grid Layout */
.stocks-in-group {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

/*  StockCard  widgets */
.stocks-in-group > * {
  width: 100%;
}

/*  stocks-in-group  hover  */
.stocks-in-group:hover {
  /*  hover  */
}

.loading-with-skeleton {
  /*  */
}

.error {
  text-align: center;
  padding: 2rem;
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 8px;
  margin: 1rem 0;
}

.no-data {
  text-align: center;
  padding: 3rem;
  color: #666;
  background-color: #f8f9fa;
  border-radius: 8px;
}

/*  */

/*  () - 1200px  */
@media (min-width: 1200px) {
  .stocks-in-group {
    gap: 2.5rem;
  }
  
  .sector-group {
    padding: 2rem;
  }
}

/*  () - 768px  1199px */
@media (min-width: 768px) and (max-width: 1199px) {
  .stocks-in-group {
    gap: 2rem;
  }
  
  .sector-group {
    padding: 1.5rem;
  }
}

/*  () - 768px  */
@media (max-width: 767px) {
  .stock-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
  
  .update-info {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.25rem;
  }
  
  .stocks-in-group {
    gap: 1.5rem;
  }
  
  .sector-group {
    padding: 1rem;
    margin: 0 -0.5rem; /*  sector group  */
  }
  
  .sector-title {
    font-size: 1rem;
  }
}

/*  () - 480px  */
@media (max-width: 480px) {
  .stock-overview {
    margin-bottom: 1rem;
  }
  
  .sector-groups {
    gap: 1.5rem;
  }
  
  .sector-group {
    padding: 0.75rem;
    margin: 0 -0.25rem;
  }
  
  .stocks-in-group {
    gap: 1rem;
  }
}
</style>